@import 'normalize.scss';
@import 'reset.scss';
@import 'fonts.scss';

//=== VARIABLES ========================================================================
$color-white: #fff;
$color-bg: #E5E5E5;
$color-text: #263056;
$color-stars: #FFB84E;
//=== /VARIABLES =======================================================================

// === COMMON ==========================================================================
*, *:before, *:after {
    box-sizing: border-box;
}
.nopadding {
    padding: 0 !important;
}
.container {
    max-width: 1174px;
    margin: 0 auto;
    padding: 0 20px;
}
.main-button {
    display: inline-block;
    background: $color-text;
    border-radius: 6px;
    color: $color-white !important;
    text-decoration: none !important;
}
.dropdown-btn {
    font-family: 'Avenir Next';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    letter-spacing: -0.24px;
    color: $color-text;
}
h1 {
    font-family: 'Cormorant Garamond';
    font-style: normal;
    font-weight: bold;
    font-size: 60px;
    line-height: 54px;
    color: $color-white;
}
h2 {
    font-family: 'Avenir Next';
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    color: $color-text;
}
a,
p {
    font-family: 'Avenir Next';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-text;
    text-decoration: none !important;
}
.navigation-link {
    border-bottom: 2px solid transparent;
}
.navigation-link:hover {
    color: $color-text !important;
    border-bottom: 2px solid $color-text;
}
// === HEADER ==============================================================================
.header {
    height: 88px;
    display: flex;
    align-items: center;
    .section-inner {
        padding: 0 50px;
        width: 100%;
        .navigation {
            display: flex;
            align-items: center;
            height: 88px;
            &__logo {
                margin-right: 80px;
                .white {
                    display: none;
                }
            }
            &__burger {
                display: none;
            }
            &__nav {
                display: flex;
                align-items: center;
                width: 100%;
                height: 88px;
                &__dropdown {
                    display: flex;
                    align-items: center;
                    height: 88px;
                    &__item {
                        display: flex;
                        align-items: center;
                        button {
                            height: 100%;
                            width: 186px;
                            border-radius: 0;
                            border: 0;
                            font-family: 'Avenir Next';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 22px;
                            &:focus {
                                box-shadow: none;
                                background: #f4f4f8;
                            }
                            .fa-chevron-down {
                                margin-left: 5px;
                                color: $color-text;
                            }
                        }
                        &.show {
                            .fa-chevron-down {
                                transform: rotate(180deg);
                            }
                        }
                    }
                    .dropdown {
                        .dropdown-toggle::after {
                            display: none;
                        }
                    }
                    .dropdown-menu.show {
                        margin: 0;
                        border: none;
                        width: 186px;
                        border-radius: 0;
                        padding: 24px 0 24px 19px;
                        li {
                            margin-bottom: 16px;
                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                        &:before {
                            content: '';
                            position: absolute;
                            left: 50%;
                            top: -14px;
                            transform: translateX(-50%);
                            display: inline-block;
                            width: 0;
                            height: 0;
                            border-left: 10px solid transparent;
                            border-right: 10px solid transparent;
                            border-bottom: 14px solid $color-white;
                        }
                    }
                }
                &__account {
                    display: flex;
                    align-items: center;
                    a {
                        display: inline-flex;
                        align-items: center;
                        img {
                            margin-right: 7px;
                        }
                        &:nth-child(2) {
                            padding: 9px 24px;
                        }
                    }
                    a + a {
                        margin-left: 40px;
                    }
                }
            }
        }
    }
}
@media (min-width: 1001px) {
    .header {
        .section-inner {
            .navigation {
                &__nav {
                    &__dropdown {
                        &__item {
                            height: 100%;
                            margin-right: 20px;
                        }
                        a {
                            margin-right: 40px;
                            &:nth-child(2) {
                                margin-right: 20px;
                            }
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }
                    &__account {
                        margin-left: auto;
                    }
                }
            }
        }
    }
}
@media (max-width: 1000px) {
    body.lock {
        overflow: hidden;
    }
    .header {
        .section-inner {
            .navigation {
                justify-content: space-between;
                &__logo {
                    position: relative;
                    z-index: 5;
                    .white {
                        display: block;
                    }
                    .black {
                        display: none;
                    }
                }
                &__burger {
                    display: block;
                    position: relative;
                    z-index: 5;
                    cursor: pointer;
                    .second {
                        display: none;
                    }
                    &.active {
                        .second {
                            display: block;
                        }
                        .first {
                            display: none;
                        }
                    }
                }
                &__nav {
                    display: block;
                    position: fixed;
                    text-align: center;
                    top: -100%;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: $color-text;
                    padding: 69px 0 0 0;
                    overflow: auto;
                    z-index: 3;
                    a {
                        display: block;
                        color: $color-white;
                        font-size: 22px;
                        line-height: 30px;
                    }
                    &.active {
                        top: 0;
                    }
                    &__dropdown {
                        display: block;
                        height: 100%;
                        margin-top: 100px;
                        a {
                            display: block;
                            margin-bottom: 48px;
                            &:last-child {
                                margin: 48px 0 0 0;
                            }
                        }
                        &__item {
                            display: inline-block;
                            button {
                                display: block;
                                font-size: 22px;
                                line-height: 30px;
                                padding-top: 0;
                                padding-bottom: 0;
                                .fa-chevron-down {
                                    color: $color-white;
                                }
                                color: $color-white;
                                &:focus {
                                    background: transparent;
                                    color: #F6D29C;
                                    .fa-chevron-down {
                                        color: #F6D29C;
                                    }
                                }
                            }
                            .dropdown-menu.show {
                                position: static !important;
                                transform: translate3d(0px, 0px, 0px) !important;
                                display: block;
                                text-align: center;
                                background: transparent;
                                padding: 0;
                                text-align: center;
                                .drop-link {
                                    margin-top: 24px;
                                    font-size: 18px;
                                    line-height: 25px;
                                }
                                &:before {
                                    display: none;
                                }
                            }
                        }
                    }
                    &__account {
                        display: none;
                    }
                }
            }
        }
    }
}
// === /HEADER =============================================================================

// === INNER ===============================================================================
.inner {
    background: url(../img/header/header-bg.jpg) center no-repeat;
    background-size: cover;
    padding: 143px 0 259px 0;
    .container {
        .inner-content {
            display: flex;
            .inner-form {
                background: $color-white;
                h2 {
                    padding: 40px 0 0 40px;
                    width: 65%;
                }
                .inner-select-box {
                    input {
                        background-image: url(../img/header/header-map.svg);
                        background-repeat: no-repeat; 
                        background-position: 18px; 
                        height: 46px;
                        width: 308px;
                        outline: none; 
                        border: 1px solid #BEC1CC;
                        border-radius: 4px;     
                        padding: 0 0 0 40px;
                        margin: 24px 40px 8px 40px;
                        font-family: 'Avenir Next';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 19px;
                        color: #BEC1CC;
                    }
                }
                .inner__dropdown__item {
                    padding: 0 40px;
                    position: relative;
                    .btn {
                        position: relative;
                        display: block;
                        padding-left: 19px;
                        width: 100%;
                        text-align: left;
                        height: 44px;
                        border: 1px solid #BEC1CC;
                        border-radius: 4px; 
                        margin-bottom: 8px;
                        outline: none;
                        font-family: 'Avenir Next';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 19px;
                        letter-spacing: -0.24px;
                        color: $color-text;
                        &:last-child {
                            margin-bottom: 0;
                        }
                        &:focus {
                            box-shadow: none;
                        }
                    }
                    .dropdown-toggle::after {
                        position: absolute;
                        right: 16px;
                        top: 50%;
                        transform: translateY(-50%);
                        color: #BEC1CC;
                    }
                    .dropdown-menu {
                        padding-left: 18px;
                    }
                }
                .inner-checkbox-form {
                    margin-left: 40px;
                    display: flex;
                    .label {
                        display: flex;
                        align-items: center;
                        margin-top: 20px;
                        &:first-child {
                            margin-right: 25px;
                        }
                        .check {
                            display: none;
                        }
                        .style {
                            display: inline-block;
                            width: 18px;
                            height: 18px;
                            border: 1px solid #263056;
                            border-radius: 50%;
                            position: relative;
                        }
                        .text {
                            margin-left: 8px;
                        }
                        .style::before {
                            content: '';
                            position: absolute;
                            display: inline-block;
                            width: 10px;
                            height: 10px;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            background-color: $color-text;
                            border-radius: 50%;
                            opacity: 0;
                        }
                        .check:checked + .style::before {
                            opacity: 1;
                        }
                    }
                }
                .inner-search-button {
                    padding-bottom: 40px;
                    .main-button {
                        display: block;
                        text-align: center;
                        max-width: 308px;
                        margin: 32px auto 0 auto;
                        padding: 11px 0;
                    } 
                }
            }
            .inner-title {
                margin-left: 291px;
                &__description {
                    h1 {
                        margin-top: 97px;
                    }
                    p {
                        color: $color-white;
                        margin-top: 24px;
                        width: 85%;
                    }
                }
            }
        }
    }
}
@media (max-width: 1050px) {
    .inner {
        .container {
            .inner-content {
                flex-wrap: wrap;
                .inner-form {
                    order: 2;
                }
                .inner-title {
                    margin: 0 0 24px 0;
                    order: 1;
                    &__description {
                        p {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width: 1000px) {
    .inner {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
}
@media (max-width: 768px) {
    .header {
        .section-inner {
            padding: 0 24px;
        }
    }
    .inner {
        padding-top: 136px;
        max-height: 881px;
        .container {
            .inner-content {
                .inner-form {
                    display: block;
                    width: 100%;
                    h2 {
                        padding-left: 24px;
                        width: 75%;
                    }
                    .inner-select-box {
                        padding: 0 24px;
                        input {
                            display: block;
                            width: 100%;
                            margin-left: 0;
                        }
                    }
                    .inner__dropdown__item {
                        padding: 0 24px;
                    }
                }
                .inner-title {
                    h1 {
                        font-size: 44px;
                        line-height: 38px;
                        margin-top: 0;
                    }
                    &__description {
                        p {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}
// === /INNER ==============================================================================